<template>
  <div v-if="store.yfp_store_id" id="app">
    <section class="sheet padding-15mm">
      <!-- Write HTML just like a web page -->
      <p class="nameTitle mb-0" v-text.trim="store.yfp_store_title"/>
      <p class="docTitle" style="margin-bottom: 8mm;"></p>
      <!--   -->
      <!-- <div class="fromTo">
        <div class="fz3">
          :<span class="bankName fz3" v-text="vars['back_name']"></span>
          <p class="personName" v-text="`${vars['back_code']} / ${vars['back_number']}`"></p>
        </div>
        <div class="fz3">
          :<span class="fromCompanyName  fz3" v-text="store.yfp_store_title"></span>
        </div>
      </div> -->
      <!-- fax -->
      <table class="table mb-0" style="margin-left: 10mm;">
        <thead>
          <tr>
            <td class="border-0 p-0">
              <div class="fz3">
                :<span class="bankName fz3" v-text="vars['back_name']"  style="color:#006fc0"></span>
                <p class="personName" v-text="`${vars['back_code']} / ${vars['back_number']}`"  style="color:#006fc0; margin-top:2mm;"></p>
              </div>
            </td>
            <td class="border-0  p-0">
              <div class="fz3 pl-5">
                :<span class="fromCompanyName  fz3" v-text="store.yfp_store_title" style="color:#006fc0"></span>
              </div>
            </td>
          </tr>
          <tr>
            <th scope="col" style="width:50%"  class="border-0 p-0">
              <div class="fz3" style="margin-bottom:2mm;">FAX:
                <span v-text.trim="store.yfp_store_touch_fax"/>
              </div>
            </th>
            <th scope="col" style="width:50%"  class="border-0 p-0 pl-5">
              <div class="fz3" style="margin-bottom:2mm;">FAX:
                <span v-text.trim="store.yfp_store_touch_fax"/>
              </div>
              
            </th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <th scope="row"  class="border-0 p-0">
              <div class="fz3 ">TEL:
                <span class="fz3" v-text.trim="store.yfp_store_tel"/>
              </div>
            </th>
            <td  class="border-0 p-0"> 
              <div class="fz3  pl-5">TEL:
                <span class="fz3" v-text.trim="store.yfp_store_tel"/>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
      <!-- <div class="fax">
        <div class="fz3">FAX:
          <span v-text.trim="vars['bank_fax']"/>
        </div>
        <div class="fz3">FAX:
          <span v-text.trim="store.yfp_store_touch_fax"/>
        </div>
      </div> -->
      <!-- Tel -->
      <!-- <div class="tel">
        <div class="fz3">TEL:
          <span class="fz3" v-text.trim="vars['bank_phone']"/>
        </div>
        <div class="fz3">TEL:
          <span class="fz3" v-text.trim="store.yfp_store_tel"/>
        </div>
      </div> -->
      <!--  -->
      <div class="detailInfo">
        <div class="all">
          <div class="left w-50">
            <div class="instructorDate">
              <p class="title mb-0"> :</p>
              <span></span>
              <span></span>
              <span></span>
            </div>
          </div>
          <div class="right">
            <div class="accountNum">
              <p class="title mb-0"> :</p>
              <span class="accountNumContent" v-text.trim="store.yfp_store_trust_number"/>
            </div>
          </div>
        </div>
        <div class="middleBox">
          <p class="title"> :</p>
          <p class="firstParagraph">
            
          </p>
          <p class="secondLine"></p>
          <p class="secondPoint">
             :
          </p>
          <p class="secondPointDetail">
             <span class="secondPointMonth">{{date}}</span>
            :
          </p>
          <p class="applyMoney">
            1-(1)(A) :
            <span class="money">${{price_a | currency}}</span>
          </p>
          <p class="expiredMoney">
            -(2)(B) : <span class="money">${{price_b | currency}}</span>
          </p>
          <p class="voucherCantUse">
            -(3)(C) : <span class="money">${{price_c | currency}}</span>
          </p>
          <!--  -->
          <p class="prepareMoney">
            2<span class="moneyName"
          >-(1)(D) :</span
          >
            <span class="money"><input type="number" v-model="price_d"/></span>
          </p>
          <p class="handlingFee">
            -(2)(E) :
            <span class="money"><input type="number" v-model="price_e"/></span>
          </p>
          <p class="showAndSellDiffer">
            -(3)(F) :
            <span class="money"><input type="number" v-model="price_f"/></span>
          </p>
          <p class="backToDes">
            
          </p>
          <!--  -->
          <p class="reduceTrustHandlingFee">
            3-(1)(A+B)* :
            <span class="percentage"> {{store.yfp_store_trust_par}} %</span>(G) :
            <span class="money">${{price_g | currency}}</span>
          </p>
          <p class="yearLowestPrice ">
            -(2)(-)(H) : $<input type="number" v-model="price_h" class="lowestPay"/>
          </p>
          <div class="bankName"> : <span v-text="vars['back_name']"></span></div>
          <div class="bankName">/ : <span v-text="`${vars['bank_full_name']}`"></span></div>
          <div class="bankName"> : <span v-text="vars['back_number']"></span></div>
          <!--  -->
          <div class="actuallyReturn">
            4(A+B+C-D-E-F-G-H ) (I) :
            <span class="money">${{price_i}}</span>
          </div>
          <div class="bankName blue"> : {{store.yfp_store_bank_name}}</div>
          <!--          <div class="bankName blue">/ : XX-XX</div>  -->
          <div class="bankName blue"> : {{store.yfp_store_apply_bank}}</div>
          <div class="bankName blue"> : {{store.yfp_store_bank_number}}</div>
        </div>
        <div class="signName">
          
          <div class="boxFlex">
            <div class="box">
              <div class="signBoxTop"></div>
              <div class="signBoxButtom"></div>
            </div>
          </div>
        </div>
      </div>
      <div class="bankSituation">
        
      </div>
    </section>
  </div>
</template>

<script>
  import './assets/apply/all.css'
  import {mapState, mapGetters, mapActions} from "vuex";
  import {mapFields} from "vuex-map-fields";

  export default {
    name: "App",
    methods: {
      ...mapActions("apply", [
        "init",
      ])
    },
    computed: {
      price_i() {
        return this.price_a + this.price_b + this.price_c - this.price_d - this.price_e - this.price_f - this.price_g - this.price_h;
      },
      ...mapFields("apply", [
        "price_d",
        "price_e",
        "price_f",
        "price_h",
      ]),
      ...mapState("apply", [
        "store",
        "price_a",
        "price_b",
        "price_c",
        "price_g",
        "date",
        "vars",
      ]),
    },
    mounted() {
      this.init();
    }
  };
</script>


<style lang="scss" scoped>
  @import "./assets/all.scss";
  .lowestPay{
    width: 120px;
  }
</style>


